.btn {
  font-size: 1.5em;
  margin: 5px;
  border-radius: 5px;
  width: 100px;
  padding: 10px;
  cursor: pointer; }
  .btn--long {
    width: 200px; }
  .btn:disabled {
    cursor: not-allowed;
    background-color: #ddd; }
  .btn--next {
    background-color: #62d6b7aa; }
  .btn--danger {
    background-color: #ce50508a; }



